import React, {useState} from 'react';
import 'font-awesome/css/font-awesome.min.css';
import {ShowInCalendarHOC} from "./ShowinCalendarHOC";


const ShowInCalendar = (props) => {

    const [isShow, setIsShow] = useState(props.isShowInCalendar);
    let ChangeIsShow = () => {
        isShow ? setIsShow(false) : setIsShow(true)
    };
    let setShowInCalendar = () => {
        props.changeShowInCalendar(props.id, true);
        ChangeIsShow();
    };
    let canceleShowInCalendar = () => {
        props.changeShowInCalendar(props.id, false);
        ChangeIsShow();
    };
    return (
        <>
            {
                props.id ?
                    !isShow ?
                        <button
                            onClick={setShowInCalendar}
                        ><i className="fa fa-calendar-check-o" aria-hidden="true"> </i>
                        </button>
                    :
                        <button
                            onClick={canceleShowInCalendar}
                        ><i className="fa fa-calendar-times-o" aria-hidden="true"> </i>
                        </button>
                :
                    <button
                        disabled='disabled'
                    > <i className="fa fa-calendar-times-o" aria-hidden="true"> </i>
                    </button>
            }
        </>
    )
};
export default ShowInCalendarHOC(ShowInCalendar);

