import React from 'react';
import CalendarDeal from "./CalendarItem";



const CatendarList = (props) => {
    console.log(props);
    let ThisDayList = props.dealList
        .filter(item => (item.id.length > 2) && (item.isShowInCalendar) &&
            (item.startDate <= props.date) &&
            (item.endingDate >= props.date))
    console.log(ThisDayList);

    let RendList = ThisDayList.map(deal => {
            return (
                <CalendarDeal
                    key={deal.id}
                    id={deal.id}
                    name={deal.name}
                    isDone={deal.done}
                    description={deal.description}
                    importance={deal.importance}
                    startDate={deal.startDate}
                    endingDate={deal.endingDate}
                    isShowInCalendar={deal.isShowInCalendar}
                    subdeals={deal.children}
                />
            )
    });

    return(RendList);
};

export default CatendarList;
