
import React, {useState} from 'react';
import style from './ReductWindow.module.css'
import classNames from "classnames";
import {ThemeStyleHOC} from "../Common/ThemeStyleHoc";
import DataChanging from "./DataChenging";
//import {setShowInCalendar} from "../../Redux/DealReducer";
import {connect} from "react-redux";
import ShowInCalendar from "../Common/Buttons/ShowInCalendar";
import Label from "../DealTree/Label/Label";
import {changeDescription} from "../../Redux/DealReducer";

const RedactWindow = (props) => {
    console.log(props);

    let theme = props.theme;

    let close = () => {props.close()};


    let [isActiv, setActiv]=useState(false);
    let [description, setDescription]  = useState(props.description);

    let changeActivity = () => {setActiv(!isActiv);};
    let onDescriptionChange = (element) => {
        setDescription(element.currentTarget.value);
    };
    let updateDescription = () => {
        props.changeDescription(props.id, description);
        changeActivity();
    };


    return(
        <div className={style.backdrop}>
            <div className={classNames(style.block, theme.redactWindow)}>
                <p className={style.name}>Name</p>
                <div className={style.nameBlock}>
                    <Label
                        name={props.name}
                        id={props.id}
                    />
                </div>
                <div className={classNames(style.impotance, theme.redactWindow__button)}>
                    <button>!</button>
                </div>
                <p className={style.date}>Data</p>
                <div className={style.firstDate}><DataChanging id={props.id} date={props.startDate} type={'s'}/></div>
                <div className={style.lastDate}><DataChanging id={props.id} date={props.endingDate} type={'e'}/></div>
                {/*<button className={style.calendarBTN} onClick={ShCal}>SC</button>*/}
                <div className={classNames(style.calendarBTN,
                    (props.startDate && props.endingDate) ? theme.redactWindow__button : theme.redactWindow__disabledButton)}
                >
                    {(props.startDate && props.endingDate) ?
                        <ShowInCalendar
                            id={props.id}
                            isShowInCalendar={props.isShowInCalendar}
                        />
                        :
                        <ShowInCalendar
                            isShowInCalendar={props.isShowInCalendar}
                        />

                    }
                </div>

                <div className={style.descriptionBlock}>
                    <p className={style.descriptionBlock__name}>Description</p>
                    <textarea
                        className={style.descriptionBlock__input}
                        defaultValue={props.description}
                        onChange={onDescriptionChange}
                        onBlur={updateDescription}
                        //onBlur={}
                    />
                </div>
                <div className={classNames(style.resultBlock, theme.redactWindow__button)}>
                    <button onClick={close}>Ok</button>
                </div>

            </div>
        </div>
    )
};

let mapStateToProps = (state) => {
    return { }
};


export default ThemeStyleHOC( connect(mapStateToProps, {changeDescription})(RedactWindow));
